.header{display:flex;align-items:center;justify-content:space-between;height:60px;position:relative}.header__menu{display:flex;align-items:center}.header__logo{font-size:1.5625rem;font-weight:700;text-transform:uppercase;margin-right:75px;cursor:pointer}@media(max-width: 799px){.header__logo{margin-right:20px}}.header__trigger{display:flex;align-items:center;position:relative}.header__trigger img{width:15px;cursor:pointer}.header__trigger>p{font-size:.9375rem;padding-left:15px;line-height:18px;cursor:pointer}.header__trigger>p::after{content:"⮟";padding-left:5px}.header__sort{position:absolute;left:0px;top:21px;width:100%;padding:10px 0px;line-height:18px;background-color:#fff;border-radius:0px 0px 10px 10px;opacity:0;transition:opacity .35s ease}.header__sort__active{opacity:1}.header__sort p{font-size:.9375rem;font-weight:400;padding:15px;transition:all .35s ease;cursor:pointer}.header__sort p:hover{background-color:#f8f8f8}.header__sort p.active{background-color:#f8f8f8}.header__cart{position:relative;width:22px;height:25px}.header__cart_img{width:24px;height:25px;cursor:pointer}.header__cart_count{display:flex;justify-content:center;align-items:center;position:absolute;top:-5px;right:-10px;width:17px;height:17px;background-color:#ffa542;border-radius:50%;font-size:.9375rem;color:#fff;line-height:18px;cursor:pointer}